:root {
  --primary-font-family: "Roboto", sans-serif;
  --secondary-font-family: "Raleway", sans-serif;
  --primary-text-color: #edf3bc;
  --secondary-text-color: #1b1b15;
  --accent-color: #815b07;
  --primary-background-color: #000;
  --secondary-background-color: #990;
  --thirty-background-color: #fff;
}

.header {
  background-color: var(--primary-background-color);
  border-bottom: 3px solid var(--secondary-background-color);
  z-index: 100;
  padding-top: 5px;
  padding-bottom: 5px;
  position: sticky;
  top: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

