  .header__container {
    padding-top: 5px;
    display: block;
  }
}

.social-list__icon {
  fill: var(--primary-text-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.social-list__icon:hover, .social-list__icon:focus {
  fill: var(--secondary-background-color);
}

