.modal {
  max-height: auto;
  width: 100%;
  max-width: 450px;
  background: #fff;
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

@media screen and (min-width: 1200px) {
  .modal {
    max-width: 528px;
  }
}

.modal__close {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #0000001a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__icon {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal__close:hover .modal__icon, .modal__close:focus .modal__icon {
  fill: var(--accent-color);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #0003;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop__formular {
  width: 100%;
  height: 100%;
  background-color: #0003;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
