.mobile-menu {
  position: fixed;
  width: 50%;
  height: 100%;
  background-color: var(--primary-background-color);
  z-index: 9;
  top: 20;
  right: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  @media screen and (min-width: 768px) {
    display: none;
  }
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow-y: scroll;
}
.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.mobile-menu__container {
  padding-top: 80px;
  padding-bottom: 48px;
  padding-left: 25px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.mobile-menu__button {
  position: absolute;
  top: 8px;
  right: 10px;
  border: none;
  background-color: transparent;
  padding: 11px 11px;
  line-height: 0;
  fill: var(--secondary-background-color);
  &:hover,
  &:focus {
    fill: var(--accent-color);
  }
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.mobile-nav__item {
  &:not(:last-child) {
    margin-bottom: 25px;
  }
}
.nav-field {
  margin-bottom: 30px;
}
.mobile-nav__link {
  color: var(--primary-text-color);
  font-family: var(--primary-font-family);
  font-weight: 500;
  font-size: 20px;
  line-height: 1.18;
  letter-spacing: 0.02em;
  &:hover,
  &:focus {
    color: var(--accent-color);
  }
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.mobile-social-list {
  display: flex;
  align-items: center;
}
.mobile-social-list__item {
  padding: 4px 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  &:not(:last-child) {
    margin-right: 10px;
  }
}
.mobile-social-list__icon {
  fill: var(--primary-text-color);
  .mobile-social-list__link:hover &,
  .mobile-social-list__link:focus & {
    fill: var(--accent-color);
  }
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
